<template>
  <div>
    <div class="block">每日一题</div>
    <h3 class="timu">题目：{{ this.timu }}</h3>
    <mt-radio class="xuanxiang" v-model="value" :options="options" @change="check">
      {{ value }}
    </mt-radio>
  </div>
</template>
<script type="text/javascript">
import { Radio } from 'mint-ui'
import { Toast } from 'mint-ui'

export default {
  name: 'meiri',
  data() {
    return {
      value: '',
      daan: 'B',
      timu: 'xxxxxxxxxxxxxxxxxxxxxxx?',
      options: [{
        label: '选项A',
        value: 'A'
      }, {
        label: '选项B',
        value: 'B'
      }, {
        label: '选项C',
        value: 'C'
      }]
    }
  },
  methods: {
    check() {
      // console.log(this.value)
      if (this.value == this.daan) {
        Toast({
          message: '回答正确'
        })
      } else {
        // alert()
        Toast({
          message: `回答错误,正确答案是${this.daan}`
        })
      }
    }
  }
}

</script>
<style type="text/css">
.block {
  width: 100%;
  height: 35px;
  background-color: #50BFFF;
  text-align: center;
  /*color: #5da9ff;*/
  font-size: 25px;
}

.timu {
  margin: 30% 0 20% 10%;
}

.xuanxiang {
  width: 90%;
  margin: 5%;
}

</style>



